import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import parse from "html-react-parser";
import { ToastContainer, toast } from "react-toastify";
import { addToCart, removeFromCart } from "../redux/CartSlice";
import { Box, Card, CardContent, Chip, Paper, Tab, Tabs } from "@mui/material";
import { ShoppingCart, Calendar, Plus, Minus, Tag } from "lucide-react";
import { addToCart1, removeFromCart1 } from "../redux/CartSlice1";
import { Badge, message } from "antd";
import NotificationBanner from "./Notification";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";

const ShopDetail1 = () => {
  const dispatch = useDispatch();
  const navigation = useNavigate();
  const params = useParams();

  let showText = Cookies.get("AllTextHead");

  const cart = useSelector((state) => state?.cart1);

  // const encryptedUser = Cookies.get('user');
  //  const user = encryptedUser ? JSON.parse(CryptoJS.AES.decrypt(encryptedUser , encryptionKey).toString(CryptoJS.enc.Utf8)):null ;
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedUser = Cookies.get("user");
  const user = encryptedUser
    ? JSON.parse(
        CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;
  const textEncy = Cookies.get("AllText");
  const AllText = textEncy
    ? JSON.parse(
        CryptoJS.AES.decrypt(textEncy, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  const [product, setProduct] = useState([]);
  const [products, setProducts] = useState([]);

  const [selectedWeight, setSelectedWeight] = useState({});
  const [selectedImage, setSelectedImage] = useState("");
  const [tabValue, setTabValue] = useState(0);

  useEffect(() => {
    getProducts();
    // getFruits();
    getCategories();
    getSubcategories();
  }, []);
  const [categoryData, setCategoryData] = useState([]);
  const [subcategoryData, setSubcategoryData] = useState([]);
  const getCategories = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/getCategory",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setCategoryData(
          res.data?.category.filter((item) => item.status === "Active")
        );
      }
    } catch (error) {
      console.error("Error fetching categories:", error);
    }
  };

  const getSubcategories = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/getSubcategory",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setSubcategoryData(
          res.data?.Subcategory.filter((item) => item.status === "Active")
        );
      }
    } catch (error) {
      console.error("Error fetching subcategories:", error);
    }
  };
  const [data, setData] = useState([]);
  const getProducts = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/Getproductlist",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        const activeProducts = res.data.ProductList.filter(
          (item) => item.status === "Active"
        );
        setProduct(activeProducts);
        setData(activeProducts);
        const initialSelectedWeight = {};
        activeProducts.forEach((product) => {
          initialSelectedWeight[product._id] = !user?.club
            ? product.weight?.[0]
            : product.Clubweight?.[0];
        });
        setSelectedWeight(initialSelectedWeight);
      }
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };
  // const getFruits = async () => {
  //   try {
  //     const res = await axios.get(
  //       "https://majesticmall.net/api/admin/Getfruitslist",
  //       {
  //         headers: {
  //           Authorization: `Bearer ${token}`,
  //         },
  //       }
  //     );
  //     if (res.status === 200) {
  //       const activeProducts = res.data.fruitsList.filter(
  //         (item) => item.status === "Active"
  //       );
  //       setProducts(activeProducts);
  //       setData(activeProducts);
  //       const initialSelectedWeight = {};
  //       activeProducts.forEach((product) => {
  //         initialSelectedWeight[product._id] = !user?.club
  //           ? product.weight?.[0]
  //           : product.Clubweight?.[0];
  //       });
  //       setSelectedWeight(initialSelectedWeight);
  //     }
  //   } catch (error) {
  //     console.error("Error fetching products:", error);
  //   }
  // };

  const [itemQuantities, setItemQuantities] = useState({});
  const item =
    products?.find((items) => items?._id === params.id) ||
    product?.find((items) => items?._id === params.id);
  const currentSelectedWeight1 = selectedWeight[item?._id];
  const itemQuantity = itemQuantities[item?._id] || 1;

  useEffect(() => {
    if (item?.Photos?.length > 0) {
      setSelectedImage(item.Photos[0]?.file);
    }
  }, [item]);

  const handleQuantityChange = (item, change) => {
    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item._id] || 1) + change, 1),
    }));
  };

  const [notification, setNotification] = useState({
    show: false,
    message: "",
  });

  const handleAddToCart = (item) => {
    if (item.stock - item?.sellproduct <= 0)
      return message.error("Out of stock");
    if (Object.keys(item).length == 0)
      return message.error("Product not found");
    item.weight[0].discount = item?.discount;
    item.Clubweight[0].discount = item?.clubdiscount;
    const weightToAdd =
      selectedWeight[item._id] ||
      (!user?.club ? item.weight[0] : item.Clubweight[0]);

    const quantity = itemQuantities[item._id] || 1;
    // setNotification({
    //   show: true,
    //   message: `Success: You have added ${item.productname} to your cart!`,
    // });
    message.success(
      `Success: You have added ${item?.productname} to your cart!`
    );
    dispatch(
      addToCart1({
        ...item,
        selectedWeight: weightToAdd,
        quantity,
      })
    );
  };
  const handleRemoveFromCart = (item) => {
    const weightToRemove =
      selectedWeight[item._id] ||
      (!user?.club ? item.weight[0] : item.Clubweight[0]);

    dispatch(
      removeFromCart1({
        _id: item._id,
        selectedWeight: weightToRemove,
      })
    );

    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item._id] || 0) - 1, 0),
    }));
  };

  const scrollToTop = (val) => {
    navigation(`/shop-detail1/${val?._id}`);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const handleSubscribe = (product) => {
    if (!user) {
      toast.info("Please login to subscribe");
      return;
    }
    navigation(`/subscribe/${product._id}`, {
      state: { selectedWeight: selectedWeight[product._id] },
    });
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const [zoomStyle, setZoomStyle] = useState({});
  const [circleStyle, setCircleStyle] = useState({ display: "none" });

  const handleMouseMove = (e) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const x = ((e.clientX - rect.left) / rect.width) * 100;
    const y = ((e.clientY - rect.top) / rect.height) * 100;

    setZoomStyle({
      transformOrigin: `${x}% ${y}%`,
      transform: "scale(2)", // Adjust scale for desired zoom
    });

    setCircleStyle({
      display: "block",
      top: `${e.clientY - rect.top - 50}px`, // Adjust for circle center
      left: `${e.clientX - rect.left - 50}px`,
    });
  };

  const handleMouseLeave = () => {
    setZoomStyle({ transform: "scale(1)", transformOrigin: "center center" });
    setCircleStyle({ display: "none" });
  };
  const categoryid = categoryData?.find(
    (ele) => ele?.categoryName == "Fruits & Vegetables"
  );
  if (!item) return null;
  if (!item) return null;
  const isItemInCart = (item) => {
    return cart?.items1?.some((cartItem) => cartItem._id === item._id);
  };
  const inCart1 = isItemInCart(item);
  const cate = categoryData?.filter((e) => e._id === item.category)[0]
    ?.categoryName;
  const subcate = subcategoryData?.filter((e) => e._id === item.subcategory)[0]
    ?.SubcategoryName;
  return (
    <div className="max-w-7xl mx-auto p-4">
      <ToastContainer />
      <NotificationBanner
        message={notification.message}
        show={notification.show}
        onClose={() => setNotification({ ...notification, show: false })}
      />
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div className="space-y-2">
          <Card className="overflow-hidden relative w-[100%] ">
            <div
              className="relative bg-white"
              onMouseMove={handleMouseMove}
              onMouseLeave={handleMouseLeave}
              style={{ overflow: "hidden", height: "420px" }}
            >
              <img
                src={`https://majesticmall.net/product/${selectedImage}`}
                alt={item?.productname}
                loading="lazy"
                className="object-contain transition-transform duration-300"
                style={{
                  width: "100%",
                  height: "100%",
                  ...zoomStyle,
                }}
                onError={(e) => {
                  e.target.src = "../img/example.jpg"; // Replace with your default image URL
                }}
              />
              <div
                className="absolute w-24 h-24 rounded-full bg-black opacity-30 pointer-events-none"
                style={{
                  position: "absolute",
                  ...circleStyle,
                }}
              ></div>
              {item?.tag && (
                <span
                  className="absolute top-4 left-4 text-white px-3 py-1 rounded-full text-sm flex items-center gap-1"
                  style={{ backgroundColor: "#000" }}
                >
                  {item?.tag}
                </span>
              )}
            </div>
          </Card>

          <div className="flex gap-2 overflow-x-auto pb-2">
            {item?.Photos?.map((photo, index) => (
              <button
                key={index}
                onClick={() => setSelectedImage(photo?.file)}
                className={`flex-shrink-0 w-16 h-16 rounded-lg overflow-hidden border-2 transition-all
                  ${
                    selectedImage === photo?.file
                      ? "border-primary opacity-100"
                      : "border-transparent opacity-60 hover:opacity-100"
                  }`}
              >
                <img
                  src={`https://majesticmall.net/product/${photo?.file}`}
                  alt=""
                  className="w-full h-full object-cover"
                  loading="lazy"
                  onError={(e) => {
                    e.target.src = "../img/example.jpg"; // Replace with your default image URL
                  }}
                />
              </button>
            ))}
          </div>
        </div>

        <div className="space-y-6">
          <div>
            <h1 className="text-2xl font-semibold mb-2">{item?.productname}</h1>
            <div className="flex gap-2 flex-wrap">
              <Chip
                label={"Category : " + cate}
                sx={{ mr: 1, mb: 1 }}
                style={{ backgroundColor: "white" }}
              />
              <Chip
                label={"Sub-Category : " + subcate}
                sx={{ mb: 1 }}
                style={{ backgroundColor: "white" }}
              />
            </div>
          </div>

          {/* <div className="flex items-baseline gap-3">
            <span className="text-2xl font-bold" style={{ color: "#000" }}>
              ₹{item.offerPrice}
            </span>
            <span className="text-sm text-gray-500 line-through">
              ₹{item.price}
            </span>
          </div> */}

          <div className="flex gap-2">
            <select
              name="option[504]"
              id="input-option504"
              className="w-full p-2 border rounded-lg bg-background"
              style={{ maxWidth: "200px" }}
              onChange={(e) => {
                const selectedWeightData = JSON.parse(e.target.value);

                const selectedDiscount =
                  selectedWeightData.discountType === "discount"
                    ? item?.discount
                    : item?.clubdiscount;
                setSelectedWeight((prev) => ({
                  ...prev,
                  [item._id]: {
                    ...selectedWeightData,
                    discount: selectedDiscount,
                    discountType: selectedWeightData?.discountType,
                  },
                }));
              }}
              value={JSON.stringify(currentSelectedWeight1)}
            >
              {!user?.club &&
                item?.weight?.map((e) => (
                  <option
                    key={`weight-${e.id}`}
                    value={JSON.stringify({
                      ...e,
                      discount: item?.discount,
                      discountType: "discount",
                    })}
                  >
                    {e.value} {e.units} - Rs{" "}
                    {(e.price - e.price * (item?.discount / 100)).toFixed(2)}
                  </option>
                ))}
              {item?.Clubweight?.map((e) => (
                <option
                  key={`clubweight-${e.id}`}
                  value={JSON.stringify({
                    ...e,
                    discount: item?.clubdiscount,
                    discountType: "clubdiscount",
                  })}
                  disabled={!user || user?.club === false}
                  style={{
                    color: !user || user?.club === false ? "#aaa" : "inherit",
                  }}
                >
                  {e.value} {e.units} - Rs{" "}
                  {(e.price - e.price * (item?.clubdiscount / 100)).toFixed(2)}
                  {!user?.club && " (Club Members Only)"}
                </option>
              ))}
            </select>
            {item.stock - item?.sellproduct <= 0 ? null : (
              <div className="flex items-center gap-4">
                <div className="flex items-center gap-2 border rounded-lg p-1 bg-white">
                  <button
                    onClick={() => {
                      !AllText?.category
                        ? item.category == categoryid?._id
                          ? showText
                            ? message.error(showText)
                            : handleRemoveFromCart(item, 1)
                          : handleRemoveFromCart(item, 1)
                        : handleRemoveFromCart(item, 1);
                    }}
                    className="p-1 hover:bg-gray-100 rounded"
                  >
                    <Minus size={16} />
                  </button>
                  <span className="w-8 text-center">{itemQuantity}</span>
                  <button
                    onClick={() => {
                      !AllText?.category
                        ? item.category == categoryid?._id
                          ? showText
                            ? message.error(showText)
                            : handleQuantityChange(item, 1)
                          : handleQuantityChange(item, 1)
                        : handleQuantityChange(item, 1);
                    }}
                    className="p-1 hover:bg-gray-100 rounded"
                  >
                    <Plus size={16} />
                  </button>
                </div>
              </div>
            )}
          </div>

          {item.stock - item?.sellproduct <= 0 ? (
            <p
              style={{
                color: "red",
                paddingTop: "27px",
                textAlign: "center",
                fontWeight: "500",
              }}
            >
              Out of Stock
            </p>
          ) : (
            <div className="">
              {inCart1 ? (
                <button
                  onClick={() => handleAddToCart(item)}
                  className="flex-1 text-white px-4 py-2 rounded-lg flex items-center justify-center gap-2 hover:bg-primary/90 "
                  style={{ backgroundColor: "#f7ab27" }}
                >
                  <ShoppingCart size={18} />
                  Update Cart
                </button>
              ) : (
                <button
                  onClick={() => {
                    !AllText?.category && item.category == categoryid?._id
                      ? showText
                        ? message.error(showText)
                        : handleAddToCart(item)
                      : handleAddToCart(item);
                  }}
                  className="flex-1 text-white px-4 py-2 rounded-lg flex items-center justify-center gap-2 hover:bg-primary/90"
                  style={{
                    backgroundColor:
                      !AllText?.category && item.category == categoryid?._id
                        ? showText
                          ? "rgb(204 225 166)"
                          : "#000"
                        : "#000",
                  }}
                  // disabled={!AllText?.category&&item.category==categoryid?._id}
                >
                  <ShoppingCart size={18} />
                  Add to Cart
                </button>
              )}
              {item.subscribe === "Subscription" && (
                <button
                  onClick={() => handleSubscribe(item)}
                  className="flex-1 border border-primary px-4 py-2 rounded-lg flex items-center justify-center gap-2 hover:bg-primary/5"
                  style={{ color: "white", backgroundColor: "#0c363b" }}
                >
                  <Calendar size={18} />
                  Subscribe
                </button>
              )}
            </div>
          )}

          <Paper elevation={3} sx={{ p: 3 }}>
            <Tabs
              value={tabValue}
              onChange={(_, newValue) => setTabValue(newValue)}
            >
              <Tab
                label="Description"
                style={{ color: "#000", borderBottomColor: "#000" }}
              />
            </Tabs>
            <Box sx={{ mt: 2 }}>
              {item.description ? parse(item.description) : ""}
            </Box>
          </Paper>
        </div>
      </div>
      <div>
        <h2
          className="mx-5 text-3xl font-bold mt-10 mb-4"
          style={{ color: "#45595b" }}
        >
          Recently added products
        </h2>
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5">
          {data
            ?.filter((ele) => categoryid?._id == ele?.category)
            ?.slice(0, 5)
            ?.map((val) => {
              const currentSelectedWeight = selectedWeight[val._id];
              const itemQuantity1 = itemQuantities[val._id] || 1;
              const inCart = isItemInCart(val);

              return (
                <div
                  key={val._id}
                  className="box border rounded-lg border-#f7ab27-400 mx-auto w-full  max-w-xs shadow-lg hover:shadow-xl transition-shadow duration-300 bg-white"
                  style={{ height: "300px", marginBottom: "5px" }}
                >
                  <Badge.Ribbon text={val.tag} color="#f7ab27">
                    <div className="shop-detailrelative overflow-clip">
                      <Link to={`/shop-detail1/${val._id}`}>
                        <img
                          className="hover:scale-105 transition-transform duration-500 ease-in-out w-full rounded-lg shadow-lg"
                          src={`https://majesticmall.net/product/${val.Photos[0]?.file}`}
                          alt="Product"
                          loading="lazy"
                          style={{
                            height: "130px",
                            width: "350px",
                            marginTop: "10px",
                            objectFit: "contain",
                            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
                          }}
                          onClick={() => scrollToTop(val)}
                          onError={(e) => {
                            e.target.src = "../img/example.jpg"; // Replace with your default image URL
                          }}
                        />
                      </Link>

                      {/* <div
                    className="absolute top-1 text-white py-1 px-1 rounded-xl tag"
                    style={{ fontSize: "13px" }}
                  >
                    {val.tag}
                  </div> */}
                    </div>
                  </Badge.Ribbon>
                  <div className="text-center">
                    <Link to={`/shop-detail1/${val._id}`}>
                      <h3
                        className="text-l mt-4 font-semibold"
                        style={{ color: "#45595b", cursor: "pointer" }}
                        onClick={() => scrollToTop(val)}
                      >
                        {val.productname.slice(0, 20)}
                      </h3>
                    </Link>
                    <select
                      name="option[504]"
                      id="input-option504"
                      className="form-control1 weight resp-select-box"
                      style={{
                        maxWidth: "200px",
                        fontSize: "12px",
                        border: "1px solid #88ac46",
                      }}
                      onChange={(e) => {
                        const selectedWeightData = JSON.parse(e.target.value);

                        const selectedDiscount =
                          selectedWeightData.discountType === "discount"
                            ? val.discount
                            : val.clubdiscount;
                        setSelectedWeight((prev) => ({
                          ...prev,
                          [val._id]: {
                            ...selectedWeightData,
                            discount: selectedDiscount,
                            discountType: selectedWeightData.discountType,
                          },
                        }));
                      }}
                      value={JSON.stringify(currentSelectedWeight)}
                    >
                      {!user?.club &&
                        val.weight?.map((e) => (
                          <option
                            key={`weight-${e.id}`}
                            style={{ fontSize: "12px" }}
                            value={JSON.stringify({
                              ...e,
                              discount: val.discount,
                              discountType: "discount",
                            })}
                          >
                            {e.value} {e.units} - Rs
                            {(e.price - e.price * (val.discount / 100)).toFixed(
                              2
                            )}
                          </option>
                        ))}
                      {val.Clubweight?.map((e) => (
                        <option
                          key={`clubweight-${e.id}`}
                          value={JSON.stringify({
                            ...e,
                            discount: val.clubdiscount,
                            discountType: "clubdiscount",
                          })}
                          disabled={!user || user.club === false}
                          style={{
                            color:
                              !user || user.club === false ? "#aaa" : "inherit",
                            fontSize: "12px",
                          }}
                        >
                          {e.value} {e.units} - Rs{" "}
                          {(
                            e.price -
                            e.price * (val.clubdiscount / 100)
                          ).toFixed(2)}{" "}
                          {!user?.club && " (Club Members Only)"}
                        </option>
                      ))}
                    </select>
                  </div>
                  {val.stock - val?.sellproduct <= 0 ? (
                    <p
                      style={{
                        color: "red",
                        paddingTop: "27px",
                        textAlign: "center",
                        fontWeight: "500",
                      }}
                    >
                      Out of Stock
                    </p>
                  ) : (
                    <div className="flex justify-between items-center mt-4">
                      <div className="flex-grow flex justify-center">
                        {val.subscribe === "Subscription" ? (
                          <>
                            {" "}
                            {inCart ? (
                              <button
                                onClick={() => handleAddToCart(val)}
                                className="font-semibold px-3 py-1 text-xs text-white rounded-2xl"
                                style={{
                                  backgroundColor: "#f7ab27",
                                  color: "#fff",
                                }}
                              >
                                Update cart
                              </button>
                            ) : (
                              <button
                                onClick={() => {
                                  !AllText?.category &&
                                  val.category == categoryid?._id
                                    ? showText
                                      ? message.error(showText)
                                      : handleAddToCart(val)
                                    : handleAddToCart(val);
                                }}
                                className="font-semibold px-3 py-1 text-xs text-white rounded-2xl"
                                style={{
                                  backgroundColor:
                                    !AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText
                                        ? "rgb(204 225 166)"
                                        : "#000"
                                      : "#000",
                                  color: "#fff",
                                }}
                                // disabled={!AllText?.category&&val.category==categoryid?._id}
                              >
                                Buy Once
                              </button>
                            )}
                          </>
                        ) : (
                          <>
                            <div className="flex items-center mr-2 ">
                              <button
                                className="lg:text-lg md:text-md sm:text-sm font-semibold bg-[#ffb524] text-white rounded-full size-7 flex justify-center items-center "
                                // style={{
                                //   backgroundColor: "#ffb524",
                                //   color: "#fff",
                                //   borderRadius: "50%",
                                // }}
                                onClick={() => {
                                  !AllText?.category
                                    ? val.category == categoryid?._id
                                      ? showText
                                        ? message.error(showText)
                                        : handleRemoveFromCart(val, 1)
                                      : handleRemoveFromCart(val, 1)
                                    : handleRemoveFromCart(val, 1);
                                }}
                              >
                                -
                              </button>
                              <span className="px-3">{itemQuantity1}</span>
                              <button
                                className="lg:text-lg md:text-md sm:text-sm font-semibold bg-[#ffb524] text-white rounded-full size-7 flex justify-center items-center "
                                // style={{
                                //   backgroundColor: "#ffb524",
                                //   color: "#fff",
                                //   borderRadius: "50%",
                                // }}
                                onClick={() => {
                                  !AllText?.category
                                    ? val.category == categoryid?._id
                                      ? showText
                                        ? message.error(showText)
                                        : handleQuantityChange(val, 1)
                                      : handleQuantityChange(val, 1)
                                    : handleQuantityChange(val, 1);
                                }}
                              >
                                +
                              </button>
                            </div>
                            {inCart ? (
                              <button
                                onClick={() => handleAddToCart(val)}
                                className="font-semibold px-3 py-2 text-xs truncate text-white rounded-full bg-[#f7ab27]"
                                style={{
                                  backgroundColor: "#f7ab27",
                                  color: "#fff",
                                }}
                              >
                                Update cart
                              </button>
                            ) : (
                              <button
                                onClick={() => {
                                  !AllText?.category &&
                                  val.category == categoryid?._id
                                    ? showText
                                      ? message.error(showText)
                                      : handleAddToCart(val)
                                    : handleAddToCart(val);
                                }}
                                className="font-semibold px-3 py-2 text-xs text-white rounded-full "
                                style={{
                                  backgroundColor:
                                    !AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText
                                        ? "rgb(204 225 166)"
                                        : "#000"
                                      : "#000",
                                  color: "#fff",
                                }}
                                // disabled={!AllText?.category&&val.category==categoryid?._id}
                              >
                                Add to cart
                              </button>
                            )}
                          </>
                        )}
                      </div>
                      {val.subscribe === "Subscription" && (
                        <button
                          onClick={() => handleSubscribe(val)}
                          className="font-semibold px-3 py-1 text-xs text-white rounded-2xl"
                          style={{
                            backgroundColor: "#0c363b",
                            marginRight: "15px",
                          }}
                        >
                          Subscribe
                        </button>
                      )}
                    </div>
                  )}
                </div>
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default ShopDetail1;
